<template>
  <div class="padding-t_10" id="reportstabsdata">
    <div class="left-menu-tab new_tab">
      <div class="col-md-12 p-0">
        <div class="theme-primary partition">
          <span class="p-name text-white">{{ $t("Projectreport") }}</span>
        </div>
      </div>
      <div class="pt-2 mb-1 w-100 d-none d-lg-block">
        <div class="input-group customsearchbox">
          <input type="text" maxlength="50" :placeholder="Search" class="form-control searchcustomfilter border-right-0"
            autocomplete="off" v-model="searchQuery" />
          <div class="input-group-append">
            <span class="input-group-text bg-transparent bg-white border-left-0"><i class="fa fa-search"></i></span>
          </div>
        </div>
      </div>
      <div class="mt-2" id="divscrollsetuptabs" style="max-height: 700px">
        <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix lefttabflexible" id="upper"
          role="tablist">
          <li v-for="(tab, index) in filteredTabs" :key="index">
            <a class="nav-link left-menu" :data-toggle="'#' + tab.id" :class="{ active: activeKey === tab.index }"
              @click="activateTab(tab.index)">
              <i :class="tab.icon" aria-hidden="true"></i>&nbsp;
              {{ $t(tab.name) }}
            </a>
          </li>
        </ul>
      </div>
    </div>

    <!-- right-content here -->
    <div class="right-content">

      <div class="col-md-12 p-0">
        <div class="theme-primary partition p-actions-expand d-flex row" style="overflow: visible">
          <div class="action-headings col-md-7">
          <span class="p-name text-white" id="spnreporttext">{{
            this.$t(activeTabName)
          }}</span>
          <span class="p-actions float-right">
            <a href="javascript:;" id="ancshowmorefilter" class="p-action-btn text-white" style="display: none"
              :title="$t('Show') + ' ' + $t('More') + ' ' + $t('Filter')"><i class="fa fa-filter"
                aria-hidden="true"></i><br />{{
                  $t("Show")
                }}
              {{ $t("More") }} {{ $t("Filter") }}</a>
          </span>

          <span id="showDaterange" style="display: none" class="p-actions float-right p-2">
            <!-- @await Html.PartialAsync("~/Areas/BiReports/Views/Shared/DateRange.cshtml") -->
          </span>

          <span class="collapse-head-btns">
            <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
          </span>
        </div>
        <div class="col-md-5">
          <div class="row" style="padding: 5px 0;">
          <div class="input-group col-md-4 justify-content-end pr-0">
          <button class="btn btn-secondary" @click="goToPreviousMonth">&lt;</button>
          <button class="btn btn-secondary" @click="goToNextMonth">&gt;</button>
        </div>
        <div class="col-md-6 pr-0">
        <!-- {{ $options.filters.formatDate(monthEndDate) }} -->
        <vc-date-picker is-range class="timedate input-group" v-model="monthRange" @input="onMonthSelected">
          <template v-slot="{ inputValue, inputEvents, togglePopover }">
            <input placeholder="M/D/YYYY" class="form-control" :value="inputValue.start + ' - ' + inputValue.end"
              v-on="inputEvents" />
            <div class="input-group-append" v-on:click="togglePopover">
              <span class="input-group-text">
                <em class="fa fa-calendar"></em>
              </span>
            </div>
          </template>
        </vc-date-picker>
      </div>
      <div class="col-md-2">
        <i class="fa fa-bar-chart" aria-hidden="true"></i>
      </div>
    </div>
    </div>
        </div>
        
      </div>

      

      <div class="col-md-12 p-0">
        <div class="tab-content" id="myTabContent">
          <div class="expand-list">
            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
              <i class="icon-solid arrow-solid-left text-white" style="font-size: 9pt"></i>
            </a>
          </div>
          <div id="UserTrackingreport" class="tab-pane" :class="{ 'active show': activeKey == 0 }" v-if="activeKey == 0">
            <UserTrackingreport></UserTrackingreport>
          </div>
          <div id="ExceedTimeReport" class="tab-pane" :class="{ 'active show': activeKey == 1 }" v-if="activeKey == 1">

            <ExceedTimeReport></ExceedTimeReport>
          </div>
          <div id="LateEmployee" class="tab-pane" :class="{ 'active show': activeKey == 2 }" v-if="activeKey == 2">
            <Unproductive></Unproductive>
            <LateEmployees></LateEmployees>
          </div>
          <div id="UnproductiveApp" class="tab-pane" :class="{ 'active show': activeKey == 3 }" v-if="activeKey == 3">
            <UnproductiveApp></UnproductiveApp>
          </div>
          <div id="Ideltime" class="tab-pane" :class="{ 'active show': activeKey == 4 }" v-if="activeKey == 4">
            <Ideltime></Ideltime>
          </div>
          <div id="AppWebsiteUsage" class="tab-pane" :class="{ 'active show': activeKey == 5 }" v-if="activeKey == 5">
            <AppWebsiteUsage></AppWebsiteUsage>
          </div>
          <div id="ProductCountReport" class="tab-pane" :class="{ 'active show': activeKey == 6 }" v-if="activeKey == 6">
            <Category></Category>
          </div>
        </div>
      </div>
    </div>
    <!-- right-content End here -->
  </div>
</template>
<script>
import UserTrackingreport from "../TimerReports/UserTrackingreport.vue";
import ExceedTimeReport from "../TimerReports/ExceedTimeReport.vue";
import LateEmployees from "../TimerReports/LateEmployees.vue";
import UnproductiveApp from "../TimerReports/UnproductiveApp.vue";
import Ideltime from "../TimerReports/Ideltime.vue";
import AppWebsiteUsage from "../TimerReports/AppWebsiteUsage.vue";
import Category from "../TimerReports/Category.vue";
export default {
  components: {
    UserTrackingreport,
    ExceedTimeReport,
    LateEmployees,
    UnproductiveApp,
    Ideltime,
    AppWebsiteUsage,
    Category

  },
  data() {
    return {
      activeKey: 0,
      tabNames: [
        // /$t("Projectreport"),
        "UserTrackingReport",
        "ExceededTimeReport",
        "LateEmployees",
        "UnproductiveAppsReport",
        "IdleTimeReport",
        "AppwebsiteusageReport",
        "CategoryReport"
      ],
      searchQuery: "",
      monthRange: {
        start: this.monthStartDate,
        end: this.monthEndDate,
      },
      updateMonthRange: false,
      fromDate: "",
      ToDate: "",
      startDate: new Date(),
      endDate: new Date(),
      monthStartDate: new Date(), 
      monthEndDate: new Date(),
    };
  },
  mounted() {
    debugger;
    //var startDates = Date.now(); 
   // this.monthEndDate == null? new Date():this.monthEndDate;
    this.activateTab(0);
  },
  computed: {
    
    filteredTabs() {
      if (!this.searchQuery) {
        return this.tabNames.map((name, index) => ({ name, index, icon: this.getTabIcon(name) }));
      }

      const query = this.searchQuery.toLowerCase();
      return this.tabNames
        .map((name, index) => ({ name, index, icon: this.getTabIcon(name) }))
        .filter(tab => this.$t(tab.name).toLowerCase().includes(query));
    },
    activeTabName() {
      return this.tabNames[this.activeKey];
    }
  },
  async created() {
    debugger;
    var vm = this
    // await vm.GetDepartmentUser();
    var monthEndDate = vm.ConvertDateFormat(vm.monthEndDate);
    vm.monthEndDate = monthEndDate;
  },
  updated() {
    var vm = this;
    var data = vm.value;
    const newDate = vm.ConvertDateFormat(new Date());
    if (vm.dateType == "WK") {
      var dateBefore7Days = new Date();
      dateBefore7Days.setDate(dateBefore7Days.getDate() - 6);
      const startDate = vm.ConvertDateFormat(vm.startDate);
      const endDate = vm.ConvertDateFormat(vm.endDate);
      const dateBeforeSevenDays = vm.ConvertDateFormat(dateBefore7Days);
      if (!(startDate == newDate && endDate == dateBeforeSevenDays)) {
        const sevenDaysLater = new Date(vm.startDate);
        sevenDaysLater.setDate(vm.startDate.getDate() + 6);
        const diffTime = Math.abs(vm.endDate - vm.startDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        if (vm.endDate > sevenDaysLater || diffDays < 6) {
          vm.weekDate = {
            start: vm.startDate,
            end: sevenDaysLater,
          };
        }
      }
      //return false;
    } else if (vm.dateType == "MM") {
      var todayDate = new Date();
      const dateBefore30Days = new Date();
      var currentMonthDays = new Date(
        todayDate.getFullYear(),
        todayDate.getMonth() + 1,
        0
      ).getDate();
      dateBefore30Days.setDate(dateBefore30Days.getDate() - currentMonthDays);
      const dateBeforeThirtyDays = vm.ConvertDateFormat(dateBefore30Days);
      const monthStartDate = vm.ConvertDateFormat(vm.monthStartDate);
      const monthEndDate = vm.ConvertDateFormat(vm.monthEndDate);
      const thirtyDaysLater = new Date(vm.monthStartDate);
      thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);

      if (
        !(monthStartDate == dateBeforeThirtyDays && monthEndDate == newDate)
      ) {
        const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
        if (vm.monthStartDate > thirtyDaysLater || diffDays < 31) {
          vm.monthRange = {
            start: vm.monthStartDate,
            end: thirtyDaysLater,
          };
        }
      } else if (vm.updateMonthRange) {
        vm.monthRange = {
          start: vm.monthStartDate,
          end: thirtyDaysLater,
        };
      }
      //return false;
    }
  },
  methods: {
     onMonthSelected(value) {
      this.monthRange.start = value.start;
      this.monthRange.end = value.end;
    },
    goToPreviousMonth() {
      debugger;
      if (this.monthRange.start && this.monthRange.end) {
        const newStartDate = new Date(this.monthRange.start);
        const newEndDate = new Date(this.monthRange.end);

        newStartDate.setMonth(newStartDate.getMonth() - 1);
        newEndDate.setMonth(newEndDate.getMonth() - 1);

        this.monthRange.start = newStartDate.toISOString().split('T')[0];
        this.monthRange.end = newEndDate.toISOString().split('T')[0];
      }
    },
    goToNextMonth() {
      debugger;
      if (this.monthRange.start && this.monthRange.end) {
        const newStartDate = new Date(this.monthRange.start);
        const newEndDate = new Date(this.monthRange.end);

        newStartDate.setMonth(newStartDate.getMonth() + 1);
        newEndDate.setMonth(newEndDate.getMonth() + 1);

        this.monthRange.start = newStartDate.toISOString().split('T')[0];
        this.monthRange.end = newEndDate.toISOString().split('T')[0];
      }
    },
    ConvertDateFormat(date) {
      var newDate = new Date(date);
      var mnth = ("0" + (newDate.getMonth() + 1)).slice(-2);
      var day = ("0" + newDate.getDate()).slice(-2);
      var datavalue = [newDate.getFullYear(), mnth, day].join("-");
      return datavalue;
    },
    getTabIcon(name) {
      switch (name) {
        case "UserTrackingReport":
          return "fa fa-calendar";
        case "ExceededTimeReport":
          return "fa fa-clock-o";
        case "LateEmployees":
          return "fa fa-user-circle-o";
        case "UnproductiveAppsReport":
          return "fa fa-info-circle";
        case "IdleTimeReport":
          return "fa fa-clock-o";
        case "AppwebsiteusageReport":
          return "fa fa-desktop";
        case "CategoryReport":
          return "fa fa-file-text";
        default:
          return "";
      }
    },
    activateTab(index) {
      this.activeKey = index;
    },
    async onMonthSelected(date) {
      var vm = this;
      vm.updateMonthRange = false;
      vm.monthStartDate = date.start;
      vm.monthEndDate = date.end;
      var todayDate = new Date();
      var currentMonthDays = new Date(
        todayDate.getFullYear(),
        todayDate.getMonth() + 1,
        0
      ).getDate();
      const thirtyDaysLater = new Date(vm.monthStartDate);
      thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);
      const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      var dateBeforeThirtyDays = new Date();
      dateBeforeThirtyDays.setDate(
        dateBeforeThirtyDays.getDate() - currentMonthDays
      );
      // if (diffDays < 27) {
      //   vm.updateMonthRange = true;
      //   vm.ShowAlert(
      //     "Please select atleast 30 days.",
      //     "warning",
      //     false,
      //     vm.$t("Alert")
      //   );
      //   vm.monthEndDate = new Date();
      //   vm.monthStartDate = dateBeforeThirtyDays;
      // } else if (vm.monthEndDate > thirtyDaysLater) {
      //   vm.updateMonthRange = true;
      //   vm.ShowAlert(
      //     "You can not select more than 30 days.",
      //     "warning",
      //     false,
      //     vm.$t("Alert")
      //   );
      //   vm.monthEndDate = new Date();
      //   vm.monthStartDate = dateBeforeThirtyDays;
      // }
    },
    formatDuration(durationInHours) {
      const hours = Math.floor(durationInHours);
      const minutes = Math.round((durationInHours - hours) * 60);
      return `${hours}h ${minutes}m`;
    },
    getTimeRanges(interval) {
      const ranges = [];
      const date = new Date();
      const format = {
        hour: "numeric",
        minute: "numeric",
      };
      for (let minutes = 0; minutes < 24 * 60; minutes = minutes + interval) {
        date.setHours(0);
        date.setMinutes(minutes);
        ranges.push(date.toLocaleTimeString("en", format));
      }
      return ranges;
    },
  },
};
</script>